import React, { useState,useEffect } from "react";
import axios from 'axios'
import { PayPalButton } from 'react-paypal-button-v2'
import { Row, Col,ListGroup, Card, Image, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import Message from "../components/Message";
import Loader from '../components/Loader';
import { getOrderDetails, payOrder, deliverOrder } from '../actions/orderActions'
import { ORDER_PAY_RESET, ORDER_DELIVER_RESET} from '../constants/orderConstants'

const OrderScreen = ({ match, history }) => {
    const orderId = match.params.id

    const [sdkReady, setSdkReady] = useState(false)

    const dispatch = useDispatch()

    const orderDetails = useSelector(state => state.orderDetails)
    const { order, loading, error } = orderDetails

    const orderPay = useSelector(state => state.orderPay)
    const { success: successPay, loading: loadingPay } = orderPay

    const orderDeliver = useSelector((state) => state.orderDeliver);
    const { success: successDeliver, loading: loadingDeliver } = orderDeliver;

    const userLogin = useSelector((state) => state.userLogin);
    const { userInfo } = userLogin;

    if(!loading){

        //Calculate price
        const addDecimals = (num)=>{
            return (Math.round(num * 100) / 100).toFixed(2)
        }
    
        order.itemsPrice = addDecimals (order.orderItems.reduce((acc, item)=> acc + item.price * item.qty,0))
    }

    useEffect(()=>{
        if(!userInfo){
            history.push('/login')
        }
        const addPayPalScript = async()=>{
            const { data: clientId } = await axios.get('/api/config/paypal')
            const script = document.createElement('script')
            script.type = 'text/javascript'
            script.src = `https://www.paypal.com/sdk/js?client-id=${clientId}`
            script.async = true
            script.onload = ()=>{
                setSdkReady(true)
            }
            document.body.appendChild(script)
        }
        if(!order || successPay || successDeliver || order._id !== orderId){
            dispatch({ type: ORDER_PAY_RESET })
            dispatch({ type: ORDER_DELIVER_RESET })
            dispatch(getOrderDetails(orderId))
        }else if(!order.isPaid){
            if(!window.paypal){
                addPayPalScript()
               
            }else{
                setSdkReady(true)
            }
        }   
    },[dispatch, orderId, successPay, order, successDeliver, userInfo, history])

    //Nakon uplate preko Paypal ove akcije idu
    const succesPaymentHandler = (paymentResult)=>{
        console.log(paymentResult);
        dispatch(payOrder(orderId, paymentResult))
    }

    //Nakon sto je označeno dostavljeno
    const deliverHandler = ()=>{
        dispatch(deliverOrder(order))
    }

   
    return (
      <div>
        {userInfo && userInfo.isAdmin && (
          <Link className='btn btn-dark my-3' to='/admin/orderlist'>
            Nazad
          </Link>
        )}
        {loading ? (
          <Loader />
        ) : error ? (
          <Message variant='danger'>{error}</Message>
        ) : (
          <>
            <h1>Narudžba {order._id}</h1>
            <Row>
              <Col md={8}>
                <ListGroup variant='flush'>
                  <ListGroup.Item>
                    <h2>Dostava</h2>
                    <p>
                      <strong>Ime: </strong>
                      {order.user.name}
                    </p>
                    <p>
                      <strong>Email: </strong>
                      <a href={`mailto:${order.user.email}`}>
                        {order.user.email}
                      </a>
                    </p>
                    <p>
                      <strong>Adresa: </strong>
                      {order.shippingAddress.address},{" "}
                      {order.shippingAddress.city}{" "}
                      {order.shippingAddress.postalCode},{" "}
                      {order.shippingAddress.country}
                    </p>
                    {order.isDelivered ? (
                      <Message variant='success'>
                        Dostavljeno {order.deliveredAt}.
                      </Message>
                    ) : (
                      <Message variant='danger'>Nije dostavljeno.</Message>
                    )}
                  </ListGroup.Item>

                  <ListGroup.Item>
                    <h2>Način plaćanja</h2>
                    <p>
                      <strong>Plaćanje putem: </strong>
                      {order.paymentMethod}
                    </p>
                    {order.isPaid ? (
                      <Message variant='success'>
                        Plaćeno {order.paidAt}.
                      </Message>
                    ) : (
                      <Message variant='danger'>Nije plaćeno.</Message>
                    )}
                  </ListGroup.Item>

                  <ListGroup.Item>
                    <h2>Narudžba</h2>
                    <p>
                      <strong>Naručeni artikli: </strong>
                      {order.orderItems.length === 0 ? (
                        <Message>
                          Nemate narudžbi. Molimo naručite nešto.😊
                        </Message>
                      ) : (
                        <ListGroup>
                          {order.orderItems.map((item, index) => (
                            <ListGroup.Item key={index}>
                              <Row>
                                <Col md={1}>
                                  <Image
                                    src={item.image}
                                    alt={item.name}
                                    fluid
                                    rounded
                                  />
                                </Col>
                                <Col>
                                  <Link to={`/product/${item.product}`}>
                                    {item.name}
                                  </Link>
                                </Col>
                                <Col md={4}>
                                  {item.qty} * {item.price} ={" "}
                                  {(item.qty * item.price).toFixed(2)} KM
                                </Col>
                              </Row>
                            </ListGroup.Item>
                          ))}
                        </ListGroup>
                      )}
                    </p>
                  </ListGroup.Item>
                </ListGroup>
              </Col>
              <Col md={4}>
                <Card>
                  <ListGroup variant='flush'>
                    <ListGroup.Item>
                      <h2>Sažetak narudžbe</h2>
                    </ListGroup.Item>
                    <ListGroup.Item>
                      <Row>
                        <Col>Artikli</Col>
                        <Col>{order.itemsPrice} KM </Col>
                      </Row>
                    </ListGroup.Item>

                    <ListGroup.Item>
                      <Row>
                        <Col>Dostava</Col>
                        <Col>{order.shippingPrice} KM </Col>
                      </Row>
                    </ListGroup.Item>

                    <ListGroup.Item>
                      <Row>
                        <Col>PDV</Col>
                        <Col>{order.taxPrice} KM </Col>
                      </Row>
                    </ListGroup.Item>

                    <ListGroup.Item>
                      <Row>
                        <Col>Ukupno</Col>
                        <Col>{order.totalPrice} KM </Col>
                      </Row>
                    </ListGroup.Item>
                    {!order.isPaid && (
                      <ListGroup.Item>
                        {loadingPay && <Loader />}
                        {!sdkReady ? (
                          <Loader />
                        ) : (
                          <PayPalButton
                            amount={order.totalPrice}
                            onSuccess={succesPaymentHandler}
                          />
                        )}
                      </ListGroup.Item>
                    )}
                    {loadingDeliver && <Loader />}
                    {userInfo &&
                      userInfo.isAdmin &&
                      order.isPaid &&
                      !order.isDelivered && (
                        <ListGroup.Item>
                          <Button
                            type='button'
                            className='btn btn-block'
                            onClick={deliverHandler}
                          >
                            Označi kao dostavljeno
                          </Button>
                        </ListGroup.Item>
                      )}
                  </ListGroup>
                </Card>
              </Col>
            </Row>
          </>
        )}
      </div>
    );}

export default OrderScreen
