import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  Row,
  Col,
  Image,
  ListGroup,
  Card,
  Button,
  Form,
} from "react-bootstrap";
import Rating from "../components/Rating";
import Loader from "../components/Loader";
import Message from "../components/Message";
import Meta from "../components/Meta";
import { listProductDetails, createProductReview } from "../actions/productActions";
import { PRODUCT_CREATE_REVIEW_RESET } from '../constants/productConstants'

const ProductScreen = ({ history, match }) => {
  const [qty, setQty] = useState(1);
  const [rating, setRating] = useState(0);
  const [comment, setComment] = useState('');

  const dispatch = useDispatch();

  const productDetails = useSelector((state) => state.productDetails);
  const { loading, product, error } = productDetails;

  const productReviewCreate = useSelector((state) => state.productReviewCreate);
  const { success: successProductReview, error: errorProductReview } = productReviewCreate;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    if (successProductReview) {
      alert('Recenzija dodana')
      setRating(0)
      setComment('')
      dispatch({ type: PRODUCT_CREATE_REVIEW_RESET })
    }
    dispatch(listProductDetails(match.params.id));
  }, [dispatch, match, successProductReview]);

  const addToCartHandler = (e) => {
    e.preventDefault();
    history.push(`/cart/${match.params.id}?qty=${qty}`);
  };

  const submitHandler = (e)=>{
    e.preventDefault()
    dispatch(createProductReview(match.params.id,{
      rating,
      comment
    }))
  }

  return (
    <div>
      <Link className="btn btn-dark my-3" to="/">
        Nazad
      </Link>
      {loading ? (
        <Loader />
      ) : error ? (
        <Message variant="danger">{error}</Message>
      ) : (
        <>
        <Meta title={product.name} />
        <Row>
          <Col md={6}>
            <Image src={product.image} alt={product.name} fluid />
          </Col>
          <Col md={3}>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <h3>{product.name}</h3>
              </ListGroup.Item>
              <ListGroup.Item>
                <Rating
                  value={product.rating}
                  text={` ${product.numReviews} recenzija`}
                />
              </ListGroup.Item>
              <ListGroup.Item>Cijena: {product.price} KM</ListGroup.Item>
              <ListGroup.Item>Opis: {product.description}</ListGroup.Item>
            </ListGroup>
          </Col>
          <Col md={3}>
            <Card>
              <ListGroup variant="flush">
                <ListGroup.Item>
                  <Row>
                    <Col>Cijena:</Col>
                    <Col>
                      <strong>{product.price} KM</strong>
                    </Col>
                  </Row>
                </ListGroup.Item>
                <ListGroup.Item>
                  <Row>
                    <Col>Zaliha:</Col>
                    <Col>
                      {product.countInStock > 0
                        ? "Na stanju"
                        : "Nema na stanju"}
                    </Col>
                  </Row>
                </ListGroup.Item>
                {product.countInStock > 0 && (
                  <ListGroup.Item>
                    <Row>
                      <Col>Količina</Col>
                      <Col>
                        <Form.Control
                          as="select"
                          value={qty}
                          onChange={(e) => setQty(e.target.value)}
                        >
                          {[...Array(product.countInStock).keys()].map((x) => (
                            <option key={x + 1} value={x + 1}>
                              {x + 1}
                            </option>
                          ))}
                        </Form.Control>
                      </Col>
                    </Row>
                  </ListGroup.Item>
                )}
                <ListGroup.Item>
                  <Button
                    onClick={addToCartHandler}
                    className="btn-block"
                    type="button"
                    disabled={product.countInStock === 0}
                  >
                    Dodaj u korpu
                  </Button>
                </ListGroup.Item>
              </ListGroup>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <h2>Recenzije</h2>
            {product.reviews.length === 0 && <Message>Nema recenzija za ovaj proizvod</Message>}
            <ListGroup variant='flush'>
              {product.reviews.map(review=>(
                <ListGroup.Item key={review._id}>
                  <strong>{review.name}</strong>
                  <Rating value={review.rating}/>
                  <p>{review.createdAt.substring(0,10)}</p>
                  <p>{review.comment}</p>
                </ListGroup.Item>
              ))}
              <ListGroup.Item>
                <h2>Napišite recenziju</h2>
                {errorProductReview && <Message variant='danger'>{errorProductReview}</Message>}
                {userInfo ? (
                  <Form onSubmit={submitHandler}>
                    <Form.Group controlId='rating'>
                      <Form.Label>Rating</Form.Label>
                      <Form.Control as='select' value={rating} onChange={(e)=> setRating(e.target.value)}>
                        <option value="">Odaberi...</option>
                        <option value="1">1-loš </option>
                        <option value="2">2-Zadovoljavajući</option>
                        <option value="3">3-Dobar</option>
                        <option value="4">4-Vrlodobar</option>
                        <option value="5">5-Odličan</option>
                      </Form.Control>
                    </Form.Group>
                    <Form.Group controlId='comment'>
                       <Form.Label>Komentar</Form.Label>
                       <Form.Control as='textarea' row='3' value={comment} onChange={(e)=> setComment(e.target.value)}>

                       </Form.Control>
                    </Form.Group>
                    <Button type='submit' variant='primary'>Potvrdi</Button>
                  </Form>
                ) : <Message>Molimo <Link to='/login'> prijavite se</Link> prije komentarisanja proizvoda</Message>}
              </ListGroup.Item>              
            </ListGroup>                
          </Col>
        </Row>
      </>
      )}
    </div>
  );
};

export default ProductScreen;
